.nav-menu__item {
  margin-right: 10px;
  font-size: 14px;
  color: #808080;
}

.nav-menu__item:hover {
  text-decoration: underline;
  color: #007bff;
}

.logo h3 > a {
  text-decoration: none;
  color: #17a2b8;
  text-transform: uppercase;
}

.nav-bar__adBtn {
  font-size: 13px;
  border: 3px solid #17a2b8 !important;
}

.search-inp {
  border-color: #17a2b8 !important;
}

.search-inp div[style="display: inline-block;"] {
  width: 90%;
}

.search-inp input {
  width: 100%;
  height: 100%;
  border: none;
  padding: 5px;
  outline: none;
}

.header-bg {
  overflow: hidden; /* прочитать */
  height: 500px;
  position: relative;
  z-index: -1;
}

.header-bg__img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
}
